import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'

import 'html5-reset/assets/css/reset.css'

import activityOffLineDetail from '@/view/activity/activity-off-line-detail.vue'; //线下活动详情页

Vue.config.productionTip = false
Vue.use(Router)

const router = new Router({
  // base: '/activity-index.html',
  routes: [{
    path: '/',
    name: 'activityOffLineDetail',
    component: activityOffLineDetail
  }, ]
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



